import { PlusOutlined } from "@ant-design/icons";
import { Button, Card, DatePicker, Popover, Tag } from "antd";
import dayjs from 'dayjs';
import type { Dayjs } from 'dayjs';
import './index.less'

const { RangePicker } = DatePicker;

const FilterHeader = ({ data }: any) => {

    const rangePresets: {
        label: string;
        value: [Dayjs, Dayjs];
    }[] = [
            { label: '最近30分钟', value: [dayjs().add(-30, 'minute'), dayjs()] },
            { label: '最近1小时', value: [dayjs().add(-1, 'h'), dayjs()] },
            { label: '最近3小时', value: [dayjs().add(-7, 'h'), dayjs()] },
            { label: '最近1天', value: [dayjs().add(-1, 'd'), dayjs()] },
            { label: '最近3天', value: [dayjs().add(-3, 'd'), dayjs()] },
            { label: '最近7天', value: [dayjs().add(-7, 'd'), dayjs()] },
            { label: '最近1个月', value: [dayjs().add(-1, 'M'), dayjs()] },
        ];

    const onRangeChange = (dates: null | (Dayjs | null)[], dateStrings: string[]) => {
        if (dates) {
            console.log('From: ', dates[0], ', to: ', dates[1]);
            console.log('From: ', dateStrings[0], ', to: ', dateStrings[1]);
        } else {
            console.log('Clear');
        }
    };

    const tagList = ['pid', '浏览器', '浏览器版本', '系统', '系统版本', '国家/地区', '省份', '城市', 'IP', '品牌', '机型', '页面url', 'session_id', 'view_id', 'env', 'release', 'use_agent', '请求参数', '网络类型', 'use_id', 'device_id'];

    const content = (
        <div style={{ maxWidth: '400px' }}>
            {
                tagList.map(item => { return (<Tag key={item} color="blue" className="filter-header-tag">{item}</Tag>) })
            }
        </div>
    );
    return (
        <Card>
            <RangePicker showTime presets={rangePresets} onChange={onRangeChange} defaultValue={[dayjs().add(-1, 'd'), dayjs()]} />


            <Popover placement="bottomLeft" trigger='click' content={content} arrow={false}>
                <Button icon={<PlusOutlined />} style={{ marginLeft: '8px' }}>筛选条件</Button>
            </Popover>
        </Card>
    );
};

export default FilterHeader;