import { getResourceList } from "@/api/modules/web/resource";
import FilterHeader from "@/components/FilterHeader";
import { ProCard, StatisticCard } from "@ant-design/pro-components";
import { Typography, List, Tag, message } from "antd";
import Title from "antd/es/typography/Title";
import { useEffect, useState } from "react";
import { useSearchParams } from "react-router-dom";
import "./index.less";
import Paragraph from "antd/es/typography/Paragraph";
import { ResResource } from "@/api/interface/web/resource";
const { Divider } = StatisticCard;
const { Text } = Typography;


const ResourceList = () => {

    const [pageNum, setPageNum] = useState(1);
    const [pageSize, setPageSize] = useState(10);
    const [selectItem, setSelectItem] = useState<ResResource>();
    const [resourceTotal, setResourceTotal] = useState(0);
    const [resourceList, setResourceList] = useState<ResResource[]>([]);
    const [searchParams] = useSearchParams();
    const [messageApi, contextHolder] = message.useMessage();

    const initResourceList = async () => {
        const appId = searchParams.get('appId');
        if (!appId) {
            messageApi.open({
                type: 'error',
                content: 'appId不能为空!',
            });
            return;
        }
        const result = await getResourceList({ pageNum, pageSize, appId });
        if (result.data) {
            setResourceTotal(result.data.total);
            const data: ResResource[] = [];
            result.data.data.forEach(item => {
                data.push(item);
            });
            setSelectItem(data[0]);
            setResourceList(data);
        }
    };

    useEffect(() => {
        initResourceList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [])

    useEffect(() => {
        initResourceList();
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [pageNum, pageSize])

    const pagination = {
        showSizeChanger: true,
        onChange: (page: number, pageSize: number) => {
            setPageNum(page);
            setPageSize(pageSize);
        },
        defaultCurrent: pageNum,
        total: resourceTotal
    }

    return (<div>
        <FilterHeader></FilterHeader>
        <div style={{ marginTop: '16px', display: 'flex', flexDirection: 'row' }}>{contextHolder}
            <List
                className="request-list"
                itemLayout="vertical"
                size="default"
                pagination={pagination}
                bordered={true}
                dataSource={resourceList}
                renderItem={(item) => (
                    <List.Item key={item.resourceId} className={selectItem?.resourceId === item.resourceId ? "selected" : ""} onClick={() => { setSelectItem(item) }}>
                        <div className="request-item">
                            <div className="request-item__left">
                                <div>
                                    <Tag bordered={false}>{item.entryType}</Tag>{new URL(item.name).hostname}
                                </div>
                                <div style={{ marginTop: '8px' }}>
                                    {new URL(item.name).pathname}
                                </div>
                            </div>
                            <div className="request-item__right">
                                <Text type="success">90</Text>
                            </div>
                        </div>
                    </List.Item>
                )}
            />
            <div className="content">
                <ProCard bordered>
                    <div>
                        <Title level={5}>请求路径：</Title>
                        <Paragraph>
                            {selectItem?.name}
                        </Paragraph>
                        <StatisticCard.Group>
                            <StatisticCard
                                statistic={{
                                    title: '请求数',
                                    value: 10,
                                }}
                            />
                            <Divider />
                            <StatisticCard
                                statistic={{
                                    title: '用户数',
                                    value: 10,
                                }}
                            />
                            <Divider />
                            <StatisticCard
                                statistic={{
                                    title: '慢请求数',
                                    value: 10,
                                }}
                            />
                            <Divider />
                        </StatisticCard.Group>
                    </div>
                </ProCard>
            </div>
        </div>
    </div>);
}

export default ResourceList;